// Optimized PDF download utility functions
let html2canvas;
let jsPDF;

// Dynamically import libraries only on client side with preloading
if (typeof window !== "undefined") {
  // Preload libraries immediately when module loads
  Promise.all([
    import("html2canvas"),
    import("jspdf")
  ]).then(([html2canvasModule, jsPDFModule]) => {
    html2canvas = html2canvasModule.default;
    jsPDF = jsPDFModule.default;
  });
}

// Optimized content creation with virtual rendering
const createOptimizedDownloadContent = (roadmapData) => {
  const downloadDiv = document.createElement("div");
  downloadDiv.className = "roadmap-download-content";

  // Optimized styles - removed expensive properties and simplified
  const baseStyles = {
    padding: "30px",
    color: "#2c3e50",
    backgroundColor: "white",
    fontFamily: "Arial, sans-serif", // Use system font for faster rendering
    fontSize: "14px",
    lineHeight: "1.5", // Reduced from 1.6 for faster rendering
    maxWidth: "750px", // Slightly smaller for faster processing
    margin: "0 auto",
    // Add these for better performance
    transform: "translateZ(0)", // Hardware acceleration
    willChange: "transform", // Optimize for changes
  };

  Object.assign(downloadDiv.style, baseStyles);

  // Optimized title
  const title = document.createElement("h1");
  Object.assign(title.style, {
    textAlign: "center",
    marginBottom: "25px",
    fontSize: "28px", // Slightly smaller
    fontWeight: "700",
    color: "#2c3e50",
    borderBottom: "2px solid #3498db", // Thinner border
    paddingBottom: "10px"
  });
  title.textContent = "Excel Expert Roadmap";
  downloadDiv.appendChild(title);

  // Batch DOM operations for better performance
  const fragment = document.createDocumentFragment();

  roadmapData.forEach((section) => {
    const sectionDiv = document.createElement("div");
    Object.assign(sectionDiv.style, {
      marginBottom: "30px", // Reduced spacing
      pageBreakInside: "avoid"
    });

    // Optimized section header
    const header = document.createElement("h2");
    Object.assign(header.style, {
      backgroundColor: "#f8f9fa", // Lighter color for faster rendering
      padding: "12px 16px", // Reduced padding
      borderRadius: "6px", // Smaller radius
      borderLeft: "4px solid #3498db", // Thinner border
      fontSize: "18px", // Slightly smaller
      fontWeight: "600",
      color: "#2c3e50",
      marginBottom: "15px"
    });
    header.textContent = `${section.id}. ${section.title}`;
    sectionDiv.appendChild(header);

    // Optimized description
    const desc = document.createElement("p");
    Object.assign(desc.style, {
      marginBottom: "20px",
      fontStyle: "italic",
      fontSize: "14px", // Consistent size
      color: "#6c757d", // Standard Bootstrap gray
      lineHeight: "1.5",
      padding: "0 8px"
    });
    desc.textContent = section.description;
    sectionDiv.appendChild(desc);

    // Create sections more efficiently
    const sections = [
      { title: "âœ… What to Learn", data: section.content.whatToLearn, color: "#28a745" },
      { title: "ðŸ“š Best Courses (English)", data: section.content.bestCourses.english, color: "#007bff" },
      { title: "ðŸ“š Best Courses (Hindi)", data: section.content.bestCourses.hindi, color: "#007bff" },
      { title: "ðŸ§° Tools to Use", data: section.content.toolsToUse, color: "#fd7e14" },
      { title: "ðŸ“˜ Docs & Websites", data: section.content.docsAndWebsites, color: "#dc3545" },
      { title: "ðŸ’¡ Project Ideas", data: section.content.projectIdeas, color: "#6f42c1" }
    ];

    sections.forEach(({ title, data, color }) => {
      if (data && data.length > 0) {
        const container = document.createElement("div");
        container.style.marginBottom = "20px";

        const titleEl = document.createElement("h3");
        Object.assign(titleEl.style, {
          fontSize: "16px", // Smaller font
          fontWeight: "600",
          color: color,
          marginBottom: "10px",
          borderBottom: `1px solid ${color}`, // Thinner border
          paddingBottom: "4px"
        });
        titleEl.textContent = title;
        container.appendChild(titleEl);

        const list = document.createElement("ul");
        Object.assign(list.style, {
          paddingLeft: "20px",
          margin: "10px 0"
        });

        // Batch create list items
        const listHTML = data.map(item => 
          `<li style="margin-bottom: 6px; font-size: 13px; line-height: 1.4; color: #495057;">${item}</li>`
        ).join('');
        list.innerHTML = listHTML;

        container.appendChild(list);
        sectionDiv.appendChild(container);
      }
    });

    fragment.appendChild(sectionDiv);
  });

  downloadDiv.appendChild(fragment);
  return downloadDiv;
};

// Optimized canvas rendering with chunking
const renderCanvasOptimized = async (element) => {
  return await html2canvas(element, {
    scale: 1.5, // Reduced from 2 for faster processing while maintaining quality
    useCORS: true,
    logging: false,
    letterRendering: false, // Disable for speed
    allowTaint: true,
    backgroundColor: "#ffffff",
    width: element.scrollWidth,
    height: element.scrollHeight,
    scrollX: 0,
    scrollY: 0,
    // Performance optimizations
    foreignObjectRendering: false, // Faster rendering
    imageTimeout: 5000, // Shorter timeout
    removeContainer: true, // Clean up faster
    async: true, // Async rendering
    // Quality vs speed balance
    quality: 0.92 // Slightly lower quality for speed
  });
};

// Enhanced progress tracking
const createProgressTracker = (callback) => {
  let progress = 0;
  const steps = [
    "Preparing content...",
    "Rendering layout...",
    "Generating canvas...",
    "Creating PDF pages...",
    "Finalizing document...",
    "Complete!"
  ];

  return {
    update: (step) => {
      progress = step;
      callback(steps[step] || `Processing... ${step}%`, (step / (steps.length - 1)) * 100);
    }
  };
};

// Main optimized PDF download function
export const handleDownloadPDF = async (roadmapData, setDownloading, onProgress = null) => {
  if (!html2canvas || !jsPDF) {
    // Show loading state and retry
    const checkLibraries = () => {
      if (html2canvas && jsPDF) {
        handleDownloadPDF(roadmapData, setDownloading, onProgress);
      } else {
        setTimeout(checkLibraries, 500);
      }
    };
    alert("PDF libraries are loading. Starting download...");
    checkLibraries();
    return;
  }

  setDownloading(true);

  const progress = onProgress ? createProgressTracker(onProgress) : { update: () => {} };

  try {
    progress.update(0);

    // Use requestIdleCallback for better performance
    await new Promise(resolve => {
      if (window.requestIdleCallback) {
        requestIdleCallback(resolve);
      } else {
        setTimeout(resolve, 0);
      }
    });

    progress.update(1);

    // Create optimized content
    const downloadDiv = createOptimizedDownloadContent(roadmapData);

    // Use invisible positioning for faster rendering
    Object.assign(downloadDiv.style, {
      position: "fixed",
      top: "-9999px",
      left: "-9999px",
      zIndex: "-1000",
      visibility: "hidden"
    });

    document.body.appendChild(downloadDiv);

    progress.update(2);

    // Force layout calculation
    downloadDiv.offsetHeight;

    // Render canvas with optimizations
    const canvas = await renderCanvasOptimized(downloadDiv);

    progress.update(3);

    // Clean up DOM immediately
    document.body.removeChild(downloadDiv);

    // Create PDF with optimized settings
    const pdf = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format: "a4",
      compress: true,
      precision: 1, // Reduced precision for speed
      userUnit: 1.0
    });

    // Optimized image processing
    const imgWidth = 210;
    const imgHeight = (canvas.height * imgWidth) / canvas.width;
    const pageHeight = 297;

    progress.update(4);

    // Convert canvas to JPEG with optimized quality
    const imgData = canvas.toDataURL("image/jpeg", 0.85); // Balanced quality vs size

    let heightLeft = imgHeight;
    let position = 0;
    let pageCount = 0;

    // Add pages more efficiently
    while (heightLeft > 0 || pageCount === 0) {
      if (pageCount > 0) {
        pdf.addPage();
      }

      pdf.addImage(
        imgData,
        "JPEG",
        0,
        position,
        imgWidth,
        imgHeight
      );

      heightLeft -= pageHeight;
      position = heightLeft - imgHeight;
      pageCount++;
    }

    progress.update(5);

    // Save with optimized filename
    const timestamp = new Date().toISOString().slice(0, 10);
    pdf.save(`Excel_Expert_Roadmap_${timestamp}.pdf`);

    // Clean up canvas
    canvas.width = 0;
    canvas.height = 0;

  } catch (error) {
    console.error("Error generating PDF:", error);
    alert("Error generating PDF. Please try again or check your browser's memory settings.");
  } finally {
    setDownloading(false);
    if (onProgress) onProgress("Complete!", 100);
  }
};

// Utility function to preload libraries for even faster subsequent downloads
export const preloadPDFLibraries = () => {
  if (!html2canvas || !jsPDF) {
    Promise.all([
      import("html2canvas"),
      import("jspdf")
    ]).then(([html2canvasModule, jsPDFModule]) => {
      html2canvas = html2canvasModule.default;
      jsPDF = jsPDFModule.default;
    });
  }
};

// Memory cleanup utility
export const cleanupPDFResources = () => {
  // Force garbage collection hint
  if (window.gc) {
    window.gc();
  }
};